.nav {
  background: #0f5769;
  height: 80px;
  display: flex;
  width: 100%;
  align-items: center;
  font-size: 1rem;
  position: absolute;
  justify-content: space-between;
  top: 0;
  z-index: 10;
  border-bottom-left-radius: 30px;
}

.navBarContainer {
  display: flex;
  height: 80px;
  width: 100%;
  padding: 0 24px;
  z-index: 1;
}
.navLogo {
  color: #fff;
  justify-content: flex-start;
  cursor: pointer;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  margin-left: 24px;
  font-weight: bold;
  text-decoration: none;
}
.navLogo:hover {
  color: #1bb7ff;
}
.navh3 {
  color: #fff;
  justify-content: flex-start;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  margin-left: 24px;
  font-weight: bold;
  text-decoration: none;
}
.navRight {
  background: #fff;
  padding: 20px 50px;
  align-items: center;
  width: 100%;
  display: flex;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}

.navRight .menu {
  position: relative;
  margin-right: 100px;
}

.navRight a {
  margin-right: 20px;
  font-size: 14px;
}

.navRight i {
  margin-right: 20px;
  font-size: 14px;
}
.navRight a:hover {
  color: #1bb7ff;
}

.navRight .subMenu {
  margin-right: 10px;
  display: flex;
  align-items: center;
  position: absolute;
  right: 0;
  font-size: 12px;
}

.navRightBtnLink {
  border-radius: 50px;
  background-color: #fff;
  padding: 10px 22px;
  margin-right: 50px;
  color: #1bb7ff;
  border: solid 1px;
  cursor: pointer;
}
.navRightBtnLink:hover {
  background-color: #1bb7ff;
  color: #fff;
  font-size: 12px;
}
.MobileIcon {
  display: none;
}
.navMenu {
  display: flex;
  align-items: center;
  list-style: none;
  text-align: center;
}
.navItem {
  height: 80px;
}
.navLinks {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0 1rem;
  height: 100%;
  cursor: pointer;
}
.navLinks.active {
  border-bottom: 3px solid #01bf71;
}
.navBtn {
  display: flex;
  align-items: center;
}
.navBtnLink {
  border-radius: 50px;
  background-color: #fff;
  padding: 10px 22px;
  color: #1bb7ff;
  font-size: 12px;
  outline: none;
  border: solid 1px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
}
.navBtnLink:hover {
  transition: all 0.2s ease-in-out;
  background: #1bb7ff;
  color: #fff;
}

@media screen and (max-width: 1024px) {
  .navRight {
    display: none;
  }
  .MobileIcon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #fff;
  }
  .navMenu {
    display: none;
  }
  .navBtn {
    display: none;
  }
}
@media screen and (max-width: 960px) {
  .nav {
    transition: 0.8s all ease;
  }
  .navBarContainer {
    transition: 0.8s all ease;
  }
}

@media screen and (max-width: 768px) {
  .navRight {
    display: none;
  }
  .MobileIcon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #fff;
  }
  .navMenu {
    display: none;
  }
  .navBtn {
    display: none;
  }
}
