/* Menus */
.menu-container {
  margin-top: 80px;
  padding: 20px;
  width: 100%;
}
.menus-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, 1fr);
  align-items: center;
}

